<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure class="section-masthead__media">
				<lazy-image :src="imgSrc" :alt="imgAlt"/>
			</figure>
			<section class="section-masthead__text">
				<h1>Bayer - Lasonil</h1>
				<p class="subtitle">Launching a new product with a new product</p>
			</section>
		</header>
		<article class="work">
			<section class="work__detail">
				<h3 class="work__subtitle">Context</h3>
				<p>
					Lasonil is one of the most well-known consumer health brands in Italy. In 2015 the brand decided to
					innovate the market with a new oral product to fight in the area of back and joint pain against
					leader brand Voltaren.
				</p>
				<p>
					Our challenge was to communicate the new, disruptive news to a public that has always known the
					brand for different reasons (a mild product with topical action against bumps and bruises).
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Insight</h3>
				<p>
					We wanted to engage an older demographic +45 who are starting to suffer from back and joint pain,
					but are still active and determined to not let anything ruin their active days.
				</p>
				<p>
					A behaviour that was totally in line with our new brand purpose about inspiring people to make every
					day count, and with our distinctive functional benefit of providing up to 12h relief from pain.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Creative idea</h3>
				<p>
					The Rusty Man was the idea that symbolizes in a very direct and impactful way the problem of feeling
					rusty and slow in your movements when affected by back or joint pain.
				</p>
				<p>
					The ad has a strong focus on the 12h relief as this benefit wasn’t currently claimed by any
					competitor in the same category.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Impact</h3>
				<p>
					The campaign helped the new brand get a 3.4M sell-out value in the first year of launch and 2.7
					market share.
				</p>
			</section>
		</article>
		<video-wrapper src="//www.youtube.com/embed/LADLAhA7hkM"
		               title="Bayer - Aspirin C"/>
		<app-footer/>
	</section>
</template>

<script>

	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import VideoWrapper from './VideoWrapper';

	export default {
		name: 'BayerLasonil',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
			VideoWrapper,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead__media {
		height: 100%;
		position: relative;
	}
	
	.section-masthead__media::after {
		content: '';
		position: absolute;
		top: -7px;
		left: 7px;
		width: 100%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		font-size: 5vw;
	}
	
	p {
		margin-bottom: 0;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	.work__detail {
		border: 1px solid var(--color__dark-alpha);
		padding: 0.75em 1.25em;
	}
	
	.work__subtitle {
		background-color: var(--color__light);
		padding: 0 0.5em;
		transform: translateY(-1.5em);
		margin: 0;
		display: inline-block;
	}
	
	.work__subtitle + p {
		transform: translateY(-1.5em);
	}
	
	ul {
		margin: 0;
		list-style: none;
		padding-left: 0;
	}
	
	li {
		position: relative;
		padding-left: 1em;
	}
	
	li:not(:first-child) {
		margin-top: 0.75em;
	}
	
	li::before {
		content: '-';
		transform: scaleX(3) rotate(20deg);
		position: absolute;
		left: 0;
		top: 0;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
		
		.work__detail:not(:first-child) {
			margin-top: 2em;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
		
		article {
			display: flex;
			background: linear-gradient(135deg, rgba(230, 230, 230, 0.2) 50%, var(--color__light-alpha-norm) 50%) no-repeat 0 -65%;
			background-size: 110% 110%;
			padding: 2em 0 0.5em 0.5em;
		}
		
		.work__detail {
			flex: 1;
		}
		
		.work__detail:not(:first-child) {
			margin-left: 1em;
		}
	}
</style>



